import React, { useState, useEffect, useCallback } from "react";
import { GlobalContext } from "./components";
import Router from "./Router";
import "./css/global.scss";
import {getBalance, getPMR, getLevel} from "./service";
import BigNumber from "bignumber.js"
import {toast} from "./utils"
let timer = null;
let useSetState = (initial = {}) => {
    const [state, saveState] = useState(initial)
    const setState = useCallback((newState) => {
      saveState(prev => ({...prev, ...newState}))
    }, [])
  
    return [state, setState]
  }
let getPrice = (level) => {
    let n_a = new BigNumber(1e-5);
    let n_b = new BigNumber(1e-7);
    return n_b.times(Number(level)).plus(n_a).toString();
}
function App() {
    const [state, setState] = useSetState({
        address: '', // 账户地址
        balance: 0, // 余额
        btcLight: '', // 合约地址
        genesis: '', // 创世账号地址
        status: 1, // 状态
        stop: 0, // 停盘额度
        surplus: 0, // 剩余额度
        PMR: '--',
        level: '--', // 层数
        levelApply: 0, // 层数已兑换dos
        rechargePrice: 0, // 兑换价格
        blackHole: '', // 兑换价格
        code: '', // 邀请码
        maxbtc: 0, // 单次最大可使用BTC
    });

    const updateStore = newState => {
        setState(newState);
    };

    const updateLevel = () => {
        getLevel().then(({data}) => {
            getLevelCtrl();
            if (data.ret) {
                const {apply, level, pmr, status, stop, surplus} = data.data;
                updateStore({
                    levelApply: apply,
                    level,
                    PMR: pmr,
                    status,
                    stop,
                    surplus,
                    rechargePrice: getPrice(level),
                })
            }
            
        }, getLevelCtrl)
    }

    const getLevelCtrl = () => {
        clearTimeout(timer);
        timer = setTimeout(() => {updateLevel()}, 2000)
    }

    useEffect( () => {
        function addr() {
            return new Promise(function (resolve) {
                setTimeout(function () {
                    // resolve({address: 'TcS8a787PfU8b754FeqWHbQFSykGUDpY4Uu'})
                    resolve({address: 'TcASfmyoFiNobNaCHeiPMkWgGmovoxk1sqk', currency: 'DOS'})
                }, 100)
            })
        }
        async function getAddress () {
            let {address, currency} = process.env.NODE_ENV === 'production'
                ? await window.demos.getIdentity()
                // : await window.demos.getIdentity();
                : await addr();

            if(currency === 'DOS') {
                updateStore({address});
                getIdentity(address);
            } else {
                toast('请切换到DOS钱包再试', 60000)
            }
        }

        getAddress();
    }, []);

    function getIdentity (address) {
        let Pa = new Promise(function (resolve) {
            getBalance(address).then(({data}) => {
                if (data.ret) {
                    let {balance, btclight, genesis, status, stop, surplus, option, blackhold, code, maxbtc} = data.data;
                    let level = option.split(',')[0] - 0;
                    let levelApply = option.split(',')[1] - 0;
                    resolve({
                        address,
                        balance,
                        btcLight: btclight,
                        genesis,
                        status,
                        stop,
                        surplus,
                        blackHole: blackhold,
                        level,
                        levelApply,
                        rechargePrice: getPrice(level),
                        code,
                        maxbtc: maxbtc ? maxbtc * 1e-8 : 0
                    })
                }
            });
        });

        let Pb = new Promise(function (resolve) {
            getPMR().then(({data}) => {
                if (data.ret) {
                    resolve({PMR: data.data})
                } else {
                    resolve({})
                }
            }, function () {
                resolve({})
            })
        });

        Promise.all([Pa, Pb]).then(function ([r1, r2]) {
            updateStore({...r1, ...r2});
            getLevelCtrl();
        });
    }

    return (
        <GlobalContext.Provider value={{ ...state, updateStore, getIdentity}}>
            <Router />
        </GlobalContext.Provider>
    );
}

export default App;
