import {axios} from '../utils'

/** 获取余额 **/
export const getBalance = (address) => {
    return axios.get(`/api/addr/${address}`)
};

/** 获取PMR **/
export const getPMR = () => {
    return axios.get('/api/pmr')
};

/** 兑换 **/
export const recharge = ({address, addr, rawtx}) => {
    return axios.post(`/api/addr/${address}/recharge/v2`, {addr, rawtx});
};

/** 兑换记录 **/
export const orders = ({address, page = 1, limit = 1000000}) => {
    return axios.get(`/api/addr/${address}/orders?page=${page}&limit=${limit}`)
};

/** 回退 **/
export const returnRecharge = ({address, id, rawtx}) => {
    return axios.post(`/api/addr/${address}/repent`, {id, rawtx});
};

/** 收益记录 **/
export const getGains = ({address, page = 1, limit = 1000000}) => {
    return axios.get(`/api/addr/${address}/gains?page=${page}&limit=${limit}`)
};

/** 基金池 **/
export const getFundsPool = () => {
    return axios.get('/api/funds')
};

/** 基金池 **/
export const getLevel = () => {
    return axios.get('/api/state')
};
