import React from "react";
import { HashRouter as Router, Route, Switch, Link } from "react-router-dom";
import { asyncComponent } from "./utils";

const Home = asyncComponent(() => import("./pages/Home"));
const Records = asyncComponent(() => import("./pages/Records"));
const ReferralCode = asyncComponent(() => import("./pages/ReferralCode"));
const Pool = asyncComponent(() => import("./pages/Pool"));
const RechargeRecords = asyncComponent(() => import("./pages/RechargeRecords"));

export default function ({children}) {
    return (
        <Router>
            <div className='wrapper'>
                <div className='main'>
                    <Switch>
                        <Route exact path="/" component={Home} />
                        <Route exact path="/records" component={Records} />
                        <Route exact path="/referral-code" component={ReferralCode} />
                        <Route exact path="/pool" component={Pool} />
                        <Route exact path="/recharge-records" component={RechargeRecords} />
                    </Switch>
                </div>
                <div className='footer'>
                    <nav className='nav-bar'>
                        <Link to='/'>首页</Link>
                        <i />
                        <Link to='recharge-records'>兑换记录</Link>
                        <i />
                        <Link to='/records'>收益记录</Link>
                        <i />
                        <Link to='/referral-code'>我的推广码</Link>
                        <i />
                        <Link to='pool'>资金池</Link>
                    </nav>
                </div>
            </div>
        </Router>
    )
};
