export default function Input (props) {
    const {value = '', placeholder = '', onClick, onChange, id, ...rest} = props;
    // instances[id] = 1;

    // function scroll(h, t) {
    //     let offset = document.querySelector('.footer').offsetHeight;
    //     let gap = 2;
    //     let innerH = window.innerHeight;
    //     document.querySelector('.main').scrollTo({top: t + h + offset + gap - innerH });
    // }

    function handleFocus(e) {
        onClick && onClick(e);
        window.demos.showKeyboard({
            value: value,
            placeholder: placeholder,
            multiple: true
        }).then(function (data){
            onChange(data)
        })

        // let h = e.target.offsetHeight;
        // let t = elementToPageTop(e.target);
        //
        // setTimeout(function () {
        //     scroll(h, t);
        // }, 400);
    }

    return (
        <input
            id={id}
            value={value}
            placeholder={placeholder}
            onClick={handleFocus}
            onChange={onChange}
            {...rest}
        />
    );
}
