import React from "react";
import './style.scss';

export default function Modal ({children, className = '', visible, onOk}) {
    const handleOk = () => {
        onOk && onOk();
    };

    return visible ? (
        <div className='c-modal-mask'>
            <div className='c-modal-content'>
                <div className='c-modal-body'>
                    {children}
                </div>
                <div className='c-modal-footer' onClick={handleOk}>确 定</div>
            </div>
        </div>
    ) : null
}
